<template>
	<div>
		<div id="addaccount_form">
						<h1 id="addaccount_title">
              <span>
                <a href="/index/roleManager">
                  <i class="el-icon-close">
                  </i>
                </a>
              </span>
            </h1>
					<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
							<el-form-item label="" prop="roleName">
								<el-input v-model="ruleForm.roleName" placeholder=''></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input type="textarea"></el-input>
							</el-form-item>
              <el-form-item>
                <el-tree
                  :data="data2"
                  :props="defaultProps"
                  show-checkbox
                  node-key="id"
                  :default-expand-all="false"
                  :expand-on-click-node="true">
                </el-tree>
              </el-form-item>
							<el-form-item>
								<el-button class='addaccount_button' type="primary" @click="submitForm('ruleForm')"></el-button>
								<el-button class='addaccount_button' @click="$router.push({path:'/index/roleManager'})"></el-button>
							</el-form-item>
					</el-form>
			</div>
	</div>
</template>
<style scoped>

  @media screen and (min-width:1367px) {
    #addaccount_form {
      /*   */
      height: 50%;
      overflow-y: scroll; 
      overflow-x: hidden;
      width: 50%;
      box-shadow: 0 5px 15px rgba(0,0,0,.5);
      position: fixed;
      display: block;
      top:62%;
      left:50%;
      margin-left:-28%;
      margin-top:-25%;  
      padding: 70px 80px 0 50px;
      margin-right: 20px;
      border: 1px solid #ccc;
      background: #fff;
      border-radius: 6px;
    }
  }

@media screen and (max-width:1367px) {
  #addaccount_form {
    height: 50%;
    width: 50%;
    box-shadow: 0 5px 15px rgba(0,0,0,.5);
    position: fixed;
    display: block;
    top: 62%;
    left: 50%;
    overflow-y: scroll;
    margin-left: -34%;
    margin-top: -24%;
    padding: 80px 150px 60px 144px;
    margin-right: 20px;
    border: 1px solid #ccc;
    background: #fff;
    border-radius: 6px;
  }
}

	.addaccount_button:nth-of-type(1) {
		background: #f87e2b;
		border: none;
	}

	.addaccount_button:nth-of-type(1):hover {
		background: rgba(248,126,43,0.9);
	}


	.addaccount_button:nth-of-type(2):hover {
		border: 1px solid rgb(248,126,43);
		color: rgb(248,126,43);
	}

	.addaccount_button {
		width: 120px;
		height: 50px;
	}

	#addaccount_title {
    position: absolute;
    left: 0px;
    top: 0px;
    padding-left: 20px;
    width: 98%;
    height: 40px;
    font-size: 18px;
    font-weight: 600;
    overflow-x: hidden;
    line-height: 40px;
    color: #444;
    border-bottom: 1px solid #eee;
	}

  #addaccount_title span {
		float: right;
		margin-right: 14px;
		cursor: pointer;
	}
</style>
      
<script>
export default {
  data () {
    return {
      ruleForm: {
        roleName: ''
      },
      rules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ]
      },
      data2: [{
        label: '',
        children: [{
          id: 1,
          label: ''
        }, {
          id: 2,
          label: ''
        }, {
          label: '',
          children: [{
            id: 3,
            label: ''
          }, {
            id: 4,
            label: '24'
          }, {
            id: 5,
            label: ''
          }, {
            id: 6,
            label: ''
          }]
        }, {
          id: 7,
          label: ''
        }, {
          label: '',
          children: [{
            id: 8,
            label: ''
          }, {
            id: 9,
            label: ''
          }]
        }, {
          id: 10,
          label: ''
        }, {
          id: 11,
          label: ''
        }, {
          id: 12,
          label: ''
        }, {
          id: 13,
          label: ''
        }, {
          label: '',
          children: [{
            id: 14,
            label: ''
          }, {
            id: 15,
            label: ''
          }]
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
        .then(() => {
          this.$router.push('/index/roleManager')
          this.$message({
            type: 'success',
            message: ''
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



